import { useEffect, useState } from 'react';
import styled from 'styled-components';
import Button from '@mui/material/Button';
import Box from '@mui/material/Box';
import Input from '../components/Input';
import Selectbox from '../components/Selectbox';
import FileUpload from '../components/fileUpload/FileUpload';
import { imageUpload } from '../api/imageUpload';
import useProducts from '../hooks/useProduct';

export default function NewProducts() {
    const initialForm = {
        productImage: [],
        productName: '',
        productPrice: '',
        productOpt: '',
        targetGender: 'female',
        productDetail: '',
        productSizes: [],
    };
    const [getProductData, setProductData] = useState(initialForm);
    const [getImageFile, setImageFile] = useState(null);
    const [isLoading, setIsLoading] = useState(false);
    const [success, setSuccess] = useState();
    const [errors, setErrors] = useState({});
    const { addProducts } = useProducts();
    const selectBoxItems = [
        { label: '', value: 'female' },
        { label: '', value: 'male' },
        { label: '', value: 'genderless' },
    ];

    const validateForm = (product, img) => {
        const checkErrors = {};

        if (!product.productName.length) checkErrors.productName = ' .';
        if (!product.productPrice.length) checkErrors.productPrice = '  .';
        if (!product.productSizes.length) checkErrors.productSizes = '  .';
        if (!product.targetGender) checkErrors.targetGender = '  .';
        if (!product.productDetail.length) checkErrors.productDetail = ' .';
        if (!img.length) checkErrors.productImage = '  .';

        setErrors(checkErrors);
        if (Object.keys(checkErrors).length) return false;
        return true;
    };

    const handleChange = e => {
        const { name, value } = e.target;
        setProductData({ ...getProductData, [name]: value });
    };

    const resetForm = () => {
        setProductData(initialForm);
        setErrors({});
        setImageFile(null);
    };

    const handleSubmit = e => {
        e.preventDefault();

        if (!validateForm(getProductData, getImageFile)) return;

        if (window.confirm(' ?')) {
            setIsLoading(true);
            imageUpload(getImageFile[0].fileObj) //
                .then(imageUrl => {
                    addProducts.mutate(
                        { product: getProductData, imageUrl },
                        {
                            onSuccess: () => {
                                setSuccess('  ');
                                setTimeout(() => {
                                    setSuccess(null);
                                    resetForm();
                                }, 4000);
                            },
                        },
                    );
                })
                .finally(() => setIsLoading(false));
        }
    };

    return (
        <>
            <Title>{success !== null ? success : ' '}</Title>
            <Box
                component="form"
                sx={{
                    '& .MuiTextField-root': { m: 1, width: '100%' },
                }}
                noValidate
                autoComplete="off"
                onSubmit={handleSubmit}>
                <FileUpload
                    setData={setImageFile}
                    currentImg={getImageFile}
                    error={errors}
                    name="productImage"
                />
                <Input
                    id="productName"
                    label=""
                    placeholder=" "
                    onChange={handleChange}
                    error={errors}
                    value={getProductData.productName}
                />
                <Input
                    id="productPrice"
                    label=""
                    type="number"
                    placeholder=" "
                    onChange={handleChange}
                    error={errors}
                    value={getProductData.productPrice}
                />
                <Selectbox
                    id="targetGender"
                    name="targetGender"
                    defaultValue="female"
                    value={getProductData.targetGender}
                    label=""
                    items={selectBoxItems}
                    onChange={handleChange}
                />
                <Input
                    id="productDetail"
                    label=""
                    placeholder="  ."
                    onChange={handleChange}
                    error={errors}
                    value={getProductData.productDetail}
                />
                <Input
                    id="productSizes"
                    label=" "
                    placeholder="   ex)XS,S,M"
                    onChange={handleChange}
                    error={errors}
                    value={getProductData.productSizes}
                />
                <Input
                    id="productOpt"
                    label=""
                    placeholder="  ."
                    onChange={handleChange}
                    error={errors}
                    value={getProductData.productOpt}
                />
                <Button
                    type="submit"
                    fullWidth
                    variant="contained"
                    size="large"
                    disabled={isLoading ? true : false}>
                    {isLoading ? ' ...' : '  '}
                </Button>
            </Box>
        </>
    );
}

const Title = styled.h2`
    font-weight: normal;
    font-size: 15px;
    text-align: center;
`;
